import Vue from 'vue'
import Router from 'vue-router'
import index from '@/pages/index'
import operatingFloor from '@/pages/gzt/operatingFloor'
Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'index',
      redirect: '/operatingFloor',
      component: index,
      children: [
        {
          path: '/operatingFloor',
          name: 'operatingFloor',
          component: operatingFloor
        }
      ]
    }
  ]
})



