import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    color: '',
    cwArr: [],
    json_key: ''
  },
  mutations: {
    getcolor(state, color) {
      state.color = color
    },
    changecwArr(state, arr) {
      state.cwArr = arr
    },
    pushArr(state, data) {
      state.cwArr.push(data)
    },
    delkz(state, obj) {
      let newcwArr = state.cwArr.filter(data => {
        return (
          data.x !== obj.data.x ||
          data.y !== obj.data.y ||
          data.color !== obj.data.color
        )
      })
      //删除
      state.cwArr = newcwArr
    },
    init(state) {
      state.color = ''
      state.cwArr.length = 0
    }
  }
})



