const canvasInit = function(_this, fu, callback) {
  var c = document.getElementById('img')
  var cxt = c.getContext('2d')
  var img = new Image()
  img.src = fu
  img.onload = function() {
    c.width = img.width
    c.height = img.height
    _this.cw.height = img.height
    _this.cw.width = img.width
    cxt.drawImage(img, 0, 0)
    callback()
  }
}
const getparentxy = function() {
  let x = document.getElementById('div').offsetLeft
  let y = document.getElementById('div').offsetTop
  return { x, y }
}

let arr = []
const beginpath = function(x, y, _this) {
  var c = document.getElementById('img')
  var cxt = c.getContext('2d')

  cxt.beginPath()
  cxt.strokeStyle = _this.$store.state.color
  if (_this.$store.state.cwArr.length !== 0) {
    var parvy = _this.$store.state.cwArr[_this.$store.state.cwArr.length - 1].y
    if (Math.abs(parvy - y) <= 4) {
      y = parvy
    }
  }
  arr.push({ x, y, color: cxt.strokeStyle })
  cxt.arc(x, arr[arr.length - 1].y, 6, 0, 2 * Math.PI)
  cxt.fillStyle = _this.$store.state.color
  cxt.fill()
  cxt.stroke()
  _this.$store.commit('pushArr', { x, y, color: cxt.strokeStyle })
}
//开始绘制
const beginpath1 = function(cwArr) {
  var c = document.getElementById('img')
  var cxt = c.getContext('2d')
  cwArr.forEach(data => {
    cxt.beginPath()
    cxt.strokeStyle = data.color
    cxt.arc(data.x, data.y, 6, 0, 2 * Math.PI)
    cxt.fillStyle = data.color
    cxt.fill()
    cxt.stroke()
  })
}
const pyl = function(c) {
  c.style.left === '' ? (c.style.left = 0) : c.style.left
  c.style.top === '' ? (c.style.top = 0) : c.style.top
  var x = parseInt(c.style.left.replace('px', ''))
  var y = parseInt(c.style.top.replace('px', ''))
  return { x, y }
}

export { canvasInit, beginpath, getparentxy, pyl, arr, beginpath1 }



