<template>
  <div class="hei">
    <el-color-picker v-model="color3"
                     show-alpha
                     @change="changeColor"
                     :predefine="predefineColors">
    </el-color-picker>
    <el-button type="primary"
               class="backbtn"
               @click="save"
               :disabled="disabled">保存</el-button>
    <el-button type="danger"
               class="backbtn"
               @click="del(del_data)"
               :disabled="delbtn">删除</el-button>
    <el-button type="warning"
               class="backbtn"
               @click="init()">恢复数据</el-button>
    <el-input v-model="input"
              class="flag backbtn"
              @change="set_json_key"
              ref="mark"
              placeholder="请输入标识值"></el-input>
  </div>
</template>
<script>
import Bus from '@/bus'
export default {
  props: ["clickbtn"],
  computed: {
    cwArr () {
      return this.$store.state.cwArr
    }
  },
  watch: {

    cwArr (data, olddata) {
      if (data.length === 0 || this.input == "") {
        this.disabled = true;
      } else {
        this.disabled = false
      }
    }
  },
  data () {
    return {
      activeIndex: '1',
      activeIndex2: '1',
      color3: 'rgba(19, 206, 102, 0.8)',
      predefineColors: [
        '#f12b30',
        '#1ba9e0',
        '#a974f7',
        '#2def4f',
        '#fcfd39'
      ],
      delbtn: true,
      disabled: true,    //是否可以保存
      del_data: {},
      input: ''
    };
  },
  mounted () {
    let _this = this;
    Bus.$on('update', function () {
      _this.disabled = true
    })
    Bus.$on('del', function (data) {
      if (data.index != -1) {
        _this.del_data = data
        _this.delbtn = false
      } else {
        _this.delbtn = true
      }
    })
    Bus.$on('input_focus', function () {
      _this.input_focus()
    })
    this.$store.state.json_key = this.input
  },
  methods: {
    set_json_key (a) {
      this.$store.state.json_key = a
    },
    init () {
      if (this.input !== "") {
        let cwArr = localStorage.getItem(this.input)
        if (cwArr !== null) {
          Bus.$emit('initCwArr', cwArr)
        }
      }

    },
    input_focus () {
      this.$refs.mark.$el.querySelector('input').focus()
    },
    test () {
      Bus.$emit('test')
    },
    del (data) {
      Bus.$emit('repeal', data)
    },
    changeColor (key) {
      this.$store.commit('getcolor', key)
    },
    save () {
      this.$store.state.cwArr
      this.$message({
        duration: 2000,
        message: `请牢记标识值：${this.input}，恢复数据请输入标识值然后点恢复按钮`
      });

      localStorage.setItem(this.input, JSON.stringify(this.$store.state.cwArr))
    }
  }
}
</script>
<style  scoped>
.flag {
  width: 150px;
}
.hei {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60px;
}
.el-header {
  padding: 0;
}
.backbtn {
  margin-left: 10px;
}
</style>


