/* eslint-disable no-console */
<template>
  <div class="operatingfloor">
    <div id="div">
      <canvas id="img"
              @dblclick="dm($event)"
              @click="recordCoord($event)"></canvas>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import fu from '@/assets/fu.png'
import { canvasInit, beginpath, getparentxy, pyl, arr, beginpath1 } from '../js/1'
import Bus from '@/bus'
import offBus from '@/mixin'
export default {
  name: 'operatingfloor',
  components: {
  },
  data () {
    return {
      url: '',
      hei: "",
      params: {
        zoomVal: 1,
        left: 0,
        top: 0,
        currentX: 0,
        currentY: 0,
        flag: false
      },
      cw: {
        width: 0,
        height: 0
      },
      imgrecord: {
        x: 0,
        y: 0
      },
      parent: {
        x: 0,
        y: 0
      }
    }
  },
  created () {
  },
  destroyed () {
    Bus.$off();
  },
  mounted () {

    this.parent.x = getparentxy().x
    this.parent.y = getparentxy().y
    this.startDrag(this, document.getElementById("img"), document.getElementById("img"))

    var _this = this;
    Bus.$on('update', function (url) {
      _this.url = url
      //数组，颜色清空
      _this.$store.commit('init');
      canvasInit(_this, url, function () {
        //callback
        //_this.$store.state.cwArr = [{ x: 120, y: 130, color: "#999" }]
        beginpath1(_this.$store.state.cwArr)
      });
    })
    Bus.$on('repeal', function (data) {
      _this.repeal(data)
    })
    Bus.$on('test', function () {
      _this.test()
    })
    Bus.$on('initCwArr', function (cwArr) {
      _this.initCwArr(cwArr)
    })
  },
  methods: {
    test () {
    },
    initCwArr (cwArr) {
      canvasInit(this, this.url, () => {
        this.$store.state.cwArr = JSON.parse(cwArr)
        canvasInit(this, this.url, () => {
          beginpath1(this.$store.state.cwArr)
        });
      });
    },
    repeal (data) {
      let arr = this.$store.state.cwArr
      var c = document.getElementById('img');
      var cxt = c.getContext('2d')
      this.$store.commit('delkz', data)
      canvasInit(this, this.url, () => {
        beginpath1(this.$store.state.cwArr)
      });
    },
    recordCoord (e) {
      Bus.$emit('del', this.pd(e))
    },
    pd (e) {
      let arr = this.$store.state.cwArr
      var c = document.getElementById('img');
      let x = e.clientX - this.parent.x - pyl(c).x
      let y = e.clientY - this.parent.y - pyl(c).y
      let flag
      var index = -1;
      let data
      if (arr.length != 0) {
        for (var i = 0; i < arr.length; i++) {
          if (Math.abs(x - arr[i].x) <= 4 && Math.abs(y - arr[i].y) <= 4) {
            flag = true
            index = i
            data = arr[i]
            break;
          }
        }
      }
      return { flag, index, data }

    },
    dm (e) {
      if (this.$store.state.json_key == '') {
        this.$message({
          message: '请在输入框输入标识值（用于二次描绘时获取数据)',
          type: 'warning',
          duration: 1500
        });
        Bus.$emit('input_focus')
        return false
      }
      var c = document.getElementById('img');
      let arr = this.$store.state.cwArr
      let x = e.clientX - this.parent.x - pyl(c).x
      let y = e.clientY - this.parent.y - pyl(c).y
      beginpath((e.clientX - this.parent.x - pyl(c).x), (e.clientY - this.parent.y - pyl(c).y), this)





    },
    //获取css属性
    getCss (o, key) {
      return o.currentStyle ? o.currentStyle[key] : document.defaultView.getComputedStyle(o, false)[key];
    },

    startDrag (_this, bar, target, callback) {
      if (_this.getCss(target, "left") !== "auto") {
        _this.params.left = _this.getCss(target, "left");
      }
      if (_this.getCss(target, "top") !== "auto") {
        _this.params.top = _this.getCss(target, "top");
      }
      //o是移动对象
      bar.onmousedown = function (event) {
        _this.params.flag = true;
        if (!event) {
          event = window.event;
          //防止IE文字选中
          bar.onselectstart = function () {
            return false;
          }
        }
        var e = event;
        _this.params.currentX = e.clientX;
        _this.params.currentY = e.clientY;
      };
      document.onmouseup = function () {
        _this.params.flag = false;
        if (_this.getCss(target, "left") !== "auto") {
          _this.params.left = _this.getCss(target, "left");
        }
        if (_this.getCss(target, "top") !== "auto") {
          _this.params.top = _this.getCss(target, "top");
        }
      };
      document.onmousemove = function (event) {
        var e = event ? event : window.event;
        if (_this.params.flag) {
          var nowX = e.clientX, nowY = e.clientY;
          var disX = nowX - _this.params.currentX, disY = nowY - _this.params.currentY;
          target.style.left = parseInt(_this.params.left) + disX + "px";
          target.style.top = parseInt(_this.params.top) + disY + "px";

          if (typeof callback == "function") {
            callback((parseInt(_this.params.left) || 0) + disX, (parseInt(_this.params.top) || 0) + disY);
          }

          if (event.preventDefault) {
            event.preventDefault();
          }
          return false;
        }
      }
    }
  }
}
</script>
<style >
body {
  margin: 0;
}
#div {
  width: 80%;
  border-radius: 6px;
  margin: 0 auto;
  height: 500px;
  overflow: hidden;
  position: relative;
  border: 2px solid #000;
}
#img {
  position: absolute;
  width: auto;
  top: 0px;
  left: 0px;
}
</style>


