<template>
  <div>
    <el-input placeholder="输入关键字进行过滤"
              v-model="filterText">
    </el-input>

    <el-tree class="filter-tree"
             :data="data2"
             :props="defaultProps"
             default-expand-all
             @node-click="cl_tree_cbk"
             :filter-node-method="filterNode"
             ref="tree2">
    </el-tree>
  </div>
</template>
<style scoped>
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  min-height: calc(100vh - 75px);
}
</style>
<script>
import Bus from '@/bus'
export default {
  watch: {
    filterText (val) {
      this.$refs.tree2.filter(val);
    }
  },

  methods: {
    cl_tree_cbk (a, b, c) {
      Bus.$emit('update', a.url)

    },
    filterNode (value, data) {
      if (!value) return true;
      return data.label.indexOf(value) !== -1;
    }
  },

  data () {
    return {
      filterText: '',
      data2: [{
        id: 1,
        label: '中正天街',
        children: [{
          id: 1.3,
          label: '地下停车场',
          url: require('@/assets/WechatIMG1.jpeg')
        }]
      }],
      defaultProps: {
        children: 'children',
        label: 'label'
      }
    };
  }
};
</script>



